<template>
    <div class="row" v-if="ready">
        <div class="col-12 flex">
            <div class="col-6" v-if="show_form"> <!--req reimb form-->
            <form id='general_req_form' enctype="multipart/form-data" class="form-horizontal" data-vv-scope="form1"  @submit.prevent="handleSubmit('form1')" >
            <div class="card">
                <div class="card-header">
                    <strong>Request Reimbursement Form</strong>
                </div>            
                <div class="card-body card-block" style='padding:15px 0 0 0'>
                    <div class="row form-group col-12">
                        <div class="col-5">
                            <label class="form-control-label">Request To :</label>
                        </div>  <!--cause fresh-->
                        <div class="col-7">
                            <select v-model="dept" class='form-control select' required>
                                <!--<option v-for="dep in dept_list" :value="dep.dept_id">{{ dep.full_dept }}</option>-->
                                <option value="CCA">Co-curricular Activity Committee</option>
                                <option value="PTA">Partent-teacher Association</option>
                            </select>
                            <!--Co-curricular Activity Committee-->
                        </div>                  
                    </div>     
                    <div class="row form-group col-12">
                        <div class="col-5">
                            <label class="form-control-label">Request Type :</label>
                        </div>  <!--cause fresh-->
                        <div class="col-7 flex">
                            <select class='form-control select' v-model='reimb_type'>
                                <option :value="'regular'">Regular Request</option>
                                <option :value="'Circular'">Fee-collecting Circular Reimbursement</option>
                            </select>
                        </div>
                    </div>
                    <div class="row form-group col-12">
                        <div class="col-5">
                            <label class="form-control-label">Procurement Excluded :</label>
                        </div>  <!--cause fresh-->
                        <div class="col-7 flex">
                            <input type="checkbox" style='margin-top:5px;margin-right:10px;' v-model="proc_exc" @click="dp_alert()"/>
                            <label style='font-size:14px;margin-top:1px;'><i>On checking, this reimbursement can be submitted directly even the amount is over $5000</i></label>
                        </div>
                    </div>
                    <table class="table table-striped first-td-padding">
                        <thead>
                            <tr>
                                <th style='width:15%'></th>
                                <th v-if="counter>=1" style='font-size:1.5em!important'>Item 1<div class="fa fa-plus" style="padding-top:0.4em;padding-left:0.8em;cursor:pointer" @click="new_row();cal_sum()"/></th>
                                <th v-if="counter>=2" style='font-size:1.5em!important'>Item 2<div class="fa fa-minus" style="padding-top:0.4em;padding-left:0.8em;cursor:pointer" @click="remove_row();cal_sum()"/></th>
                                <th v-if="counter>=3" style='font-size:1.5em!important'>Item 3<div class="fa fa-minus" style="padding-top:0.4em;padding-left:0.8em;cursor:pointer" @click="remove_row();cal_sum()"/></th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr>
                                <td style='width:15%;font-size:1.75em!important;padding-left:1.5em'>Nature</td>
                                <td v-if="counter>=1">
                                    <textarea class="form-control" maxlength="100" rows="2" v-model="nature[0]" required/>
                                </td>
                                <td v-if="counter>=2">
                                    <textarea class="form-control" maxlength="100" rows="2" v-model="nature[1]" required/>
                                </td>
                                <td v-if="counter>=3">
                                    <textarea class="form-control" maxlength="100" rows="2" v-model="nature[2]" required/>
                                </td>
                            </tr>
                            <tr>
                                <td style='width:15%;font-size:1.75em!important;padding-left:1.5em'>Location</td>
                                <td v-if="counter>=1">
                                    <select class="form-control select" v-model="loc_type[0]" @change="empty_loc(0)">
                                        <option>Internal</option>
                                        <option>External</option>
                                        <option>N/A</option>
                                    </select>
                                    <select multiple class="form-control" v-model="loc[0]" v-if="loc_type[0]=='Internal'" style='margin-top:0.55rem' required>
                                        <option v-for="loc in loc_list">{{ loc.code }}</option>
                                    </select>
                                    <input required v-if="loc_type[0]=='External'" type="text" class="form-control" style='margin-top:0.55rem' v-model="loc[0]" placeholder="Other location"/>
                                </td>
                                <td v-if="counter>=2">
                                    <select class="form-control select" v-model="loc_type[1]" @change="empty_loc(1)">
                                        <option>Internal</option>
                                        <option>External</option>
                                        <option>N/A</option>
                                    </select>
                                    <select multiple class="form-control" v-model="loc[1]" v-if="loc_type[1]=='Internal'" style='margin-top:0.55rem' required>
                                        <option v-for="loc in loc_list">{{ loc.code }}</option>
                                    </select>
                                    <input required v-if="loc_type[1]=='External'" type="text" class="form-control" style='margin-top:0.55rem' v-model="loc[1]" placeholder="Other location"/>
                                </td>
                                <td v-if="counter>=3">
                                    <select class="form-control select" v-model="loc_type[2]" @change="empty_loc(2)">
                                        <option>Internal</option>
                                        <option>External</option>
                                        <option>N/A</option>
                                    </select>
                                    <select multiple class="form-control" v-model="loc[2]" v-if="loc_type[2]=='Internal'" style='margin-top:0.55rem' required>
                                        <option v-for="loc in loc_list">{{ loc.code }}</option>
                                    </select>
                                    <input required v-if="loc_type[2]=='External'" type="text" class="form-control" style='margin-top:0.55rem' v-model="loc[2]" placeholder="Other location"/>
                                </td>
                            </tr>
                            <tr>
                                <td style='width:15%;font-size:1.75em!important;padding-left:1.5em'>Reason for application</td>
                                <td v-if="counter>=1">
                                    <textarea class="form-control" rows="2" maxlength="100" v-model="reason[0]" required/>
                                </td>
                                <td v-if="counter>=2">
                                    <textarea class="form-control" rows="2" maxlength="100" v-model="reason[1]" required/>
                                </td>
                                <td v-if="counter>=3">
                                    <textarea class="form-control" rows="2" maxlength="100" v-model="reason[2]" required/>
                                </td>
                            </tr>
                            <tr v-if="reimb_type=='Circular'">
                                <td style='width:15%;font-size:1.75em!important;padding-left:1.5em'>Circular Code</td>
                                <td v-if="counter>=1">
                                    <input type='text' class='form-control' v-model="fund[0]" placeholder="Enter Circular Code" @keyup='check_circular(0)'/>
                                </td>
                                <td v-if="counter>=2">
                                    <input type='text' class='form-control' v-model="fund[1]" placeholder="Enter Circular Code" @keyup='check_circular(1)'/>
                                </td>
                                <td v-if="counter>=3">
                                    <input type='text' class='form-control' v-model="fund[2]" placeholder="Enter Circular Code" @keyup='check_circular(2)'/>
                                </td>
                            </tr>
                            <tr v-if="(is_new_circular==true)&&(reimb_type=='Circular')">
                                <td style='width:15%;font-size:1.75em!important;padding-left:1.5em'>Circular Amount HK$</td>
                                <td v-if="reimb_type=='Circular'&&counter>=1">
                                    <input type="number" step=0.01 class="form-control" v-model='circular_val[0]' required value=0 @change='updateMaxFund(0)'/>
                                </td>
                                <td v-if="reimb_type=='Circular'&&counter>=2">
                                    <input type="number" step=0.01 class="form-control" v-model='circular_val[1]' required value=0 @change='updateMaxFund(1)'/>
                                </td>
                                <td v-if="reimb_type=='Circular'&&counter>=3">
                                    <input type="number" step=0.01 class="form-control" v-model='circular_val[2]' required value=0 @change='updateMaxFund(2)'/>
                                </td>
                            </tr>
                            <tr colspan='100%' v-if="(is_new_circular==true)&&(reimb_type=='Circular')">
                                <td style='width:15%;font-size:1.75em!important;padding-left:1.5em'>Circular (.pdf)</td>
                                <td>
                                    <input type="file" class="form-control-button" accept="application/pdf" ref="circular_file" @change="addcircular_pdf()" style="height:100%" required/>
                                </td>
                            </tr>
                            <tr>
                                <td style='width:15%;font-size:1.75em!important;padding-left:1.5em'>Amount HK$</td>
                                <td v-if="counter>=1">
                                    <input type="number" step=0.01 class="form-control" v-model='amount[0]' :max="fund_max[0]" required @change="cal_sum(0)" value=0/>
                                </td>
                                <td v-if="counter>=2">
                                    <input type="number" step=0.01 class="form-control" v-model='amount[1]' :max="fund_max[1]" required  @change="cal_sum(1)" value=0/>
                                </td>
                                <td v-if="counter>=3">
                                    <input type="number" step=0.01 class="form-control" v-model='amount[2]' :max="fund_max[2]" required  @change="cal_sum(2)" value=0/>
                                </td>
                            </tr>
                            <tr>
                                <td style='width:15%;font-size:1.75em!important;padding-left:1.5em'>Support document (.pdf)</td>
                                <td colspan='100%'> 
                                    <input type="file" class="form-control-button" accept="application/pdf" ref="pdf_file" multiple @change="addfile_pdf()" style="height:100%" required/>
                                </td>
                            </tr>
                            <tr>
                                <td style='width:15%;font-size:1.75em!important;padding-left:1.5em'>Invoice\Receipt Number</td>
                                <td colspan='100%'> 
                                    <input v-if="skip_invoice==false" type="text" class="form-control" v-model="invoice" placeholder="Separate multiple invoice with ," :required="skip_invoice==false"/>
                                    <div class='flex'>
                                        <input type='checkbox' value='na' v-model="skip_invoice" style='margin-top:4px;margin-right:8px' @change="makeid(10)"/>
                                        <label style='margin-top:2px'>No invoice number available</label>
                                    </div>                      
                                </td>
                            </tr>
                        </tbody>
                    </table>       
                </div>
                <div class="row form-group">
                    <div class="col col-md-12">
                        <div class='col-12 flex' style="display:flex;flex-direction:row">
                            <div class="col-3">Recipient type :</div>
                            <input type="radio" name="type_rec" v-model="rtype" value="staff" style="margin-top:0.2em" @click="clr" required><label>Staff</label>
                            <input type="radio" name="type_rec" v-model="rtype" value="company" style="margin-left:1em;margin-top:0.2em" @click="clr"><label>Company</label>
                        </div>
                        <div class='col-12 flex'>
                            <div class='col-3'><label>Name of recipient :</label></div>
                            <input type="text" v-model="search_rec" class="form-control input col-8"  @keyup="search_record($event)" @keydown.esc="cls_table" required/>
                        </div>
                        <!--
                        <div v-if="new_sup==true" style='padding-top:0.4em;'>
                            <div style='display:flex;flex-direction:row;width:100%;padding-left:0' class='col-12'>
                                <div class='col-8'>
                                    <input type="text" class="form-control" placeholder="Enter Company Address" v-model="company_address" required/>
                                </div>
                                <div class='col-4' style='padding-left:0;padding-right:0'>
                                    <input type="text" class="form-control" placeholder="Enter Company Contact" v-model="company_contact" required/>
                                </div>
                            </div>
                        </div>
                        -->
                    </div>
                </div>    
                <div class="card-footer">
                    <button type="submit" class="btn btn-primary btn-sm" :disabled="processing">
                    <i class="fa fa-paper-plane"></i> Submit
                    </button>
                    <button type="reset" class="btn btn-danger btn-sm" onclick="document.getElementById('general_req_form').reset()">
                    <i class="fa fa-ban"></i> Reset
                    </button>
                </div>
            </div>
            </form>
            </div> 
            <div class="col-6"> <!--Req reimb pool (show in dept)-->           
            <div class="col-xs-3 col-md-3" style='position:fixed;right:50%;top:250px;box-shadow: #888888;'>
                <v-data-table
                    :headers="headers_list"
                    :items="recep_data"
                    :search="search"
                    class="elevation-1"
                    :rows-per-page-items="[10]"
                    v-if="hv_record"                
                    >
                <template v-slot:items="props">
                    <td><a href="" :id= props.item.username v-on:click.prevent="select_user(props.item.name)">Select</a></td>
                    <td>{{ props.item.name }}</td>
                <template v-slot:pageText="props">
                    Items {{ props.pageStart }} - {{ props.pageStop }} out of {{ props.itemsLength }}
                </template>
                
                </template>
                </v-data-table> 
            </div>
             <div v-if="hv_circular" class="col-xs-3 col-md-3" style='position:fixed;right:50%;top:250px;box-shadow: #888888;'>
                <v-data-table
                    :headers="circular_headers"
                    :items="circular_list"
                    :search="search"
                    class="elevation-1"
                    :rows-per-page-items="[10]"       
                    >
                    <template v-slot:items="props">
                        <tr v-on:click="select_circular(props.item.fundsrc)" style='cursor:pointer'>
                            <td><a href="" :id= props.item.fundsrc v-on:click.prevent="select_circular(props.item.fundsrc)">Select</a></td>
                            <td>{{ props.item.fundsrc }}</td>
                        </tr>
                        <template v-slot:pageText="props">
                            Items {{ props.pageStart }} - {{ props.pageStop }} out of {{ props.itemsLength }}
                        </template>          
                    </template>
                </v-data-table> 
            </div> 
            <div class="col-12" v-if="head_view">
                <div class="card">
                    <div class="card-header">
                        <strong>Request Reimbursement Records</strong>
                    </div>  
                    <div class="card-body card-block" style='padding:0 0 0 0'>
                        <div class="flex col-12" style='margin-top:1rem;margin-bottom:1rem'>
                            <div class="col-3" style='padding-top:0.35rem;padding-left:0'>
                                <label>Select Department</label>
                            </div>
                            <div class="col-9" style='padding-right:0'>
                                <select v-model="dept_view" class='form-control select' required @change="get_reimb">
                                    <option v-for="dep in dept_list_view" :value="dep.deptmentid">{{ dep.department }}</option>
                                </select>
                            </div>
                        </div>
                        <v-data-table
                            :headers="headers"
                            :items="req_reimb_data"
                            :search="search"
                            class="elevation-1"
                            :rows-per-page-items="[10]"
                            >
                        <template v-slot:items="props">
                            <tr v-on:click="select_user(props.item.name)" style='cursor:pointer'>
                                <td><a href="" :id= props.item.username v-on:click.prevent="open_reimb(props.item.tag)">Reimbursement</a></td>
                                <td>{{ props.item.tag }}</td>
                                <td>{{ props.item.app_id }}</td>
                                <td>
                                    <div v-for="length in props.item.pdf_length">
                                        <a :href="props.item.support_doc_path[length-1]" target='_blank'>&#8226;&nbsp;{{ props.item.pdf_name[length-1] }}</a>
                                    </div>
                                </td>
                            </tr>
                        <template v-slot:pageText="props">
                            Items {{ props.pageStart }} - {{ props.pageStop }} out of {{ props.itemsLength }}
                        </template>
                        
                        </template>
                        </v-data-table>                         
                    </div>
                </div>
                </div> 
            </div> 
        </div> 
    </div>
</template>


<script>
import axios from 'axios';
export default {
    data(){          
        //var tmr =document.getElementById('tmr_date').value;
        return{
            skip_invoice:false,
            bp:['','',''],
            fund:['','',''],
            circular_pdf:'',
            is_new_circular:false,
            checking:0,
            circular_val:[0,0,0],
            circular_list:[],
            hv_circular:false,
            reimb_type:'regular',
            proc_exc:false,
            processing:false,
            head_view:false,
            loc_type:['Internal','Internal','Internal'],
            new_sup:false,
            recep_data:[],
            search_rec:'',
            rtype:'',
            company_address:'',
            company_contact:'',
            invoice:'',
            pdf:'',
            dept_view:'CCA',
            dept_list_view:[],
            show_form:true,
            req_reimb_data:[],
            dept:'CCA',
            dept_list:[],
            loc:[],
            nature:[],
            reason:[],
            amount:[],
            fund_max:[9999999,9999999,9999999],
            sum:0,
            counter:1,
            ready:false,
            hv_record:false,
            headers:[
                { text: 'Action',value :'id',sortable: false},
                { text: 'Tag',value:'tag',sortable: false},
                { text: 'Name',value:'app_id',sortable: false},
                { text: 'Invoice',value:'support_doc_path',sortable: false},
            ],
            headers_list: [
                { text: 'Action',value :'action',sortable: false},
                { text: 'Name',value:'name',sortable: false},
            ], 
            circular_headers: [
                { text: 'Action',value :'action',sortable: false},
                { text: 'Circular Code',value:'fundsrc',sortable: false},
            ],
        }  
    },
async created(){
    await axios.get('Procurement/shared_backend/check_user_setup.php')
        .then(
            (response)=>{
                var check = response.data[0].signed;
                var perm = response.data[0];
                if(!(check=='set')){
                    this.$router.push('../user_setup');
                }
            }
        )
    /*
    await axios.get('Procurement/shared_backend/department.php')
        .then(
            (response)=>{
                this.dept_list = response.data;
                this.dept = this.dept_list[0].dept_id;
            }
        ); 
    */
    await axios.get('Procurement/shared_backend/location.php')
        .then(
            (response)=>{
                this.loc_list = response.data;
            }
        ); 
    await this.dept_in();
    await this.get_reimb();
    this.ready = true;        
    //asset category of department   
  },
async mounted(){
    //asset category of department   
},   
    methods:{
        makeid(length) {
            if(this.skip_invoice==true){
                var result = '';
                var characters = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
                var charactersLength = characters.length;
                for ( var i = 0; i < length; i++ ) {
                    result += characters.charAt(Math.floor(Math.random() * charactersLength));
                }
                this.invoice = result;
                console.log(this.invoice);
            } else {
                this.invoice = '';
            }
            
        },
        updateMaxFund(event){
            this.fund_max[event] = this.circular_val[event];
            this.$forceUpdate();
        },
        select_circular(val){
            this.fund[this.checking] = val;
            this.bp[this.checking] = val;
            this.circular_max();
            this.hv_circular = false;
        },
        circular_max(){
            var formData = new FormData();
            formData.append('dept',this.dept);
            formData.append('code',this.fund[this.checking]);
            axios.post('Procurement/reimbursement/backend/circular_max.php',formData)
                .then(
                    (response)=>{
                        if(response.data!=''){
                            this.fund_max[this.checking] = response.data.balance;
                            this.$forceUpdate();
                        }
                    }
                )
        },
        check_circular(event){
            var key = this.fund[event];
            var upper = key.toUpperCase();
            this.checking = event;
            this.fund[event] = upper;
            this.bp[event] = upper;
            this.$forceUpdate();
            if(key.length>0){
                var formData = new FormData();
                formData.append('key',upper);
                formData.append('dept',this.dept);
                axios.post("Procurement/reimbursement/backend/circular_code.php",formData)
                    .then(
                        (response)=>{
                            if(response.data!='empty'){
                                this.circular_list = response.data;
                                this.is_new_circular = false;
                                this.hv_circular = true;
                            } else {
                                this.is_new_circular = true;
                                this.hv_circular = false;
                                this.fund_max[event] = 9999999;
                            }
                        }
                    )
            } else {
                this.hv_circular = false;
            }          
        },
        addcircular_pdf(event){
            var isPdf = true;
            var i=0;
            var tmp_type = '';
            this.circular_pdf = this.$refs.circular_file.files[0];
            tmp_type = this.$refs.circular_file.files[i].type;
            if(tmp_type!='application/pdf'){
                isPdf = false;
            }
            if(isPdf==true){
                this.circular_pdf =  this.$refs.circular_file.files[0];
            } else {
                this.$alert('Please upload pdf file(s) with extension (.pdf)');
                this.$refs.circular_file.value = '';
            } 
        },
        async dept_in(){
            await axios.get('Procurement/shared_backend/user_department.php')
            .then(
                (response)=>{
                    if(response.data!='empty'){
                        this.dept_list_view = response.data;
                        this.dept_view = 'CCA';
                        this.head_view = true;
                    } else {
                        this.head_view = false;
                    }
                }
            ); 
        },
        get_reimb(){
            var formData = new FormData();
            formData.append('dept',this.dept_view);
            axios.post('Procurement/reimbursement/backend/req_reimb_record.php',formData)
                .then(
                    (response)=>{
                        if(response.data!=''){
                            this.req_reimb_data = response.data;
                        } else {
                            this.req_reimb_data = [];
                        }
                    }
                )
        },
        open_reimb(id){
            document.getElementById('tmp_id').value = id;
            this.$router.push('gen_req_reimb');
        },
        new_row(event){
            if(this.counter<3){
                this.counter = this.counter+1;
            }
        },
        remove_row(event){
            if(this.counter>1){
                this.counter = this.counter-1;
            }
        },
        select_user(event){
            this.search_rec = event;
            this.hv_record = false;
            this.new_sup = false;
        },
        clr(event){
            this.search_rec='';
            this.hv_record=false;
        },
        cls_table(){
            this.hv_record=false;
        },
        cal_sum(){

        },
        search_record(event){
            if(!(event.keyCode==27)){
                var formData = new FormData();
                formData.append('key', this.search_rec);
                if(this.rtype=='staff'){
                axios.post('RequestPortal/backend/search_user_list.php',formData)
                .then(
                    (response)=>{
                        if(response.data == "Empty"){
                        this.hv_record=false; 
                        } else {
                            this.recep_data = response.data;
                            this.recep_data.username = response.data.username;
                            this.recep_data.name = response.data.name;
                            this.hv_record=true;
                        }
                    }); 
                } else if(this.rtype=='company'){
                axios.post('Procurement/shared_backend/supplier_list.php',formData)
                .then(
                    (response)=>{
                        if(response.data == "Empty"){
                        this.hv_record=false; 
                        this.new_sup=false;
                        } else if(response.data == "new"){
                            this.new_sup = true;
                        }
                        else {                       
                            this.recep_data = response.data;
                            this.recep_data.username = response.data.username;
                            this.recep_data.name = response.data.name;
                            this.hv_record=true;
                            this.new_sup=false;
                        }
                    }); 
                } 
            }
        },
        addfile_pdf(event){
            var isPdf = true;
            var i=0;
            var tmp_type = '';
            for(i=0;i<this.$refs.pdf_file.files.length;i++){
                tmp_type = this.$refs.pdf_file.files[i].type;
                if(tmp_type!='application/pdf'){
                    isPdf = false;
                }
            }
            if(isPdf==true){
                this.pdf =  this.$refs.pdf_file.files;
            } else {
            this.$alert('Please upload pdf file(s) with extension (.pdf)');
            this.$refs.pdf_file.value = '';
            }  
             //this.pdf = this.$refs.pdf_file.files;
        },
        empty_loc(id){
            this.loc[id] = '';
        },
        dp_alert(){
            if(!this.proc_exc) {
                this.$alert('I confirm that this reimbursement (over $5000) does not require procurement');          
            }
        },
        async handleSubmit(event){
            var checksum = 0;
            var cap = 5000
            if(this.proc_exc) {
                cap = 99999999;             
            } else {
                cap = 5000;
            }
            var ck = 0;
            for(ck=0;ck<this.counter;ck++){
                checksum+=parseFloat(this.amount[ck]);
            }
            if(cap > checksum){
                var count_files = 0;
                var formData = new FormData();
                var i = 0;
                formData.append('dept',this.dept);
                formData.append('invoice',this.invoice);
                formData.append('counter',this.counter);
                formData.append('circular_pdf',this.circular_pdf);
                formData.append('req_type',this.reimb_type);
                formData.append('new_circular',this.is_new_circular);
                for(i=0;i<this.counter;i++){
                    formData.append('circular_max'+i,this.circular_val[i])
                    formData.append('fundsrc'+i,this.fund[i]);
                    formData.append('bp'+i,this.bp[i]);
                    formData.append('asset_cat'+i,this.reimb_type);
                    formData.append('nature'+i,this.nature[i]);
                    formData.append('location'+i,this.loc[i]);
                    formData.append('reason'+i,this.reason[i]);
                    formData.append('amount'+i,this.amount[i]);
                }
                var i = 0;
                for(var i=0;i<this.pdf.length;i++){
                    let file = this.pdf[i];
                    formData.append('files'+i, file);
                    count_files=count_files+1;
                }
                formData.append('r_type',this.rtype);
                formData.append('newsup',this.new_sup);
                formData.append('address',this.company_address);
                formData.append('contact',this.company_contact);
                formData.append('r_name',this.search_rec);
                formData.append('count_files', count_files);
                this.processing = true;
                await axios.post('Procurement/reimbursement/backend/req_reimb_2.php',formData)
                    .then(
                        (response)=>{
                            if(response.data!='OK'){
                                this.$alert(response.data);
                            } else {
                                this.$alert('Successfully Submitted').then(
                                    ()=>{
                                        this.processing = false;                                     
                                    }
                                )      
                                this.show_form = false;
                                this.get_reimb();
                                if(this.head_view==false){
                                    this.$router.push('reimb_records');
                                }                         
                            }
                        }
                    )
            } else {
                this.$confirm("Procurement is required for reimbursement over $5000, proceed to procurement ?").then(
                    ()=>{
                        this.$router.push("../procurement/new_proc");
                    }
                )
            }
        }
    }        
}
</script>


<style scoped>
  .input-group-addon {
    background-color: transparent;
    border-left: 0;
}
input[type=date]::-webkit-inner-spin-button {
    -webkit-appearance: none;
    display: none;
}
</style>


